import React from 'react';
import { Box, Button, Flex, Image, Link, Spacer} from '@chakra-ui/react';
import Favicon from "./assets/social-media-icons/favicon.png";
import Twitter from "./assets/social-media-icons/twitter_32x32.png";
import Discord from "./assets/social-media-icons/discord_32x32.png";


const NavBar = ({ accounts, setAccounts}) => {
    const isConnected = Boolean(accounts[0]);

    async function connectAccount(){
        if(window.ethereum){
            const accounts = await window.ethereum.request({
                method: "eth_requestAccounts",

            });
            setAccounts(accounts);
        }
    }

    return (
        <Flex justify="space-between" align="center" padding="30px">
            {/* Left Side - Social Media */}
            <Flex justify="space-around" width="40%" padding="0 75px">
                <Link href="https://externals.vercel.app">
                    <Image src={Favicon} boxSize="84px" margin="0 15px" />
                </Link>
                <Link href="https://twitter.com/TheExternalss">
                    <Image src={Twitter} boxSize="42px" margin="0 15px" />
                </Link>
                <Link href="https://discord.gg/dDnEweYsnU">
                    <Image src={Discord} boxSize="42px" margin="0 15px" />
                </Link>
            </Flex>

            {/* Right Side - Sections and Connect*/}
            <Flex justify="space-around" align="center" width="30%" padding="30px 30px 30px 30px">
                <Box color="#999999" margin="0 15px" fontSize="25px">Externals</Box>
                <Spacer/>
                <Box margin="0 15px">Marketplace</Box>
                <Spacer/>
                <Box margin="0 15px">Contract</Box>
                <Spacer/>
                {/* Connect */}
            {isConnected ? (
                <Box margin="0 15px" textColor="#05f043">Connected</Box> 
            ) : (
                <Button backgroundColor="#999999"
                borderRadius="5px"
                boxShadow="0px 2px 2px 1px #0F0F0F"
                color="white"
                cursor="pointer"
                fontFamily="inherit"
                padding="15px"
                margin="0 15px"
                onClick={connectAccount}>Connect Wallet</Button>
            )}
            </Flex>
        </Flex>
        
    );
};

export default NavBar;