import {useState} from 'react';
import {ethers} from 'ethers';
import externalsTest from './ExternalsTest.json';
import { Box, Button, Flex, Input, Text, Image} from '@chakra-ui/react';
import Goku from "./assets/imgs/externalGoku.gif";

//import Web3 from 'web3';
//const ethers = require("ethers");

const externalsTestAddress = "0xaD47ebdF952eb54d826847CCeDE05f92933181AD";

const MainMint = ({accounts, setAccounts}) => {
    const [mintAmount, setMintAmount] = useState(1);
    const isConnected = Boolean(accounts[0]);

    async function handleMint(){
        if (window.ethereum){
            const provider = new ethers.providers.Web3Provider(window.ethereum);
            const signer = provider.getSigner();
            const contract = new ethers.Contract(
                externalsTestAddress,
                externalsTest.abi,
                signer
            );
            try{
                const response = await contract.mint(mintAmount, {
                    value: ethers.utils.parseEther((1 * mintAmount).toString()),
                });
                console.log('response: ', response)
            } catch (err){
                console.log("error: ", err)
            }
        }
    }

    const handleDecrement = () => {
        if(mintAmount <=1 ) return;
        setMintAmount(mintAmount - 1);
    };

    const handleIncrement = () => {
        if(mintAmount >=10 ) return;
        setMintAmount(mintAmount + 1);
    };

    return(
        <Flex justify="center" align="center" height="83vh" paddingBottom="150px">
            <Box width="520px">
                <div>
                    <Text fontSize="48px" textShadow="0 5px #00000">Externals</Text>
                    <Text 
                        fontSize="30px" 
                        letterSpacing="-5.5%" 
                        fontFamily="VT323" 
                        textShadow="0 2px 2px #00000">
                        333 Externals will join the Most Exclusive $CANTO DAO.
                    </Text>
                    <Image src={Goku} boxSize="400px" margin="0 15px" />
                </div>
            {isConnected ? (
                <div>
                    <Flex align="Center" justify="center">
                        <Button 
                        backgroundColor="#999999"
                        borderRadius="2px"
                        boxShadow="0px 2px 2px 1px #0F0F0F"
                        color = "white"
                        cursor="pointer"
                        fontFamily="inherit"
                        padding="14px"
                        marginTop="10px"
                        onClick={handleDecrement}>-</Button>
                        <Input 
                        readOnly
                        fontFamily="inherit"
                        width="100px"
                        height="40px"
                        textAlign="center"
                        paddingLeft="19px"
                        marginTop="10px"
                        type="number"
                        value={mintAmount} />
                        <Button 
                        backgroundColor="#999999"
                        borderRadius="2px"
                        boxShadow="0px 2px 2px 1px #0F0F0F"
                        color = "white"
                        cursor="pointer"
                        fontFamily="inherit"
                        padding="14px"
                        marginTop="10px"
                        onClick={handleIncrement}>+</Button>
                    </Flex>
                    <Button 
                        backgroundColor="#999999"
                        borderRadius="5px"
                        boxShadow="0px 2px 2px 1px #0F0F0F"
                        color = "white"
                        cursor="pointer"
                        fontFamily="inherit"
                        padding="15px"
                        marginTop="10px"
                        onClick={handleMint}>
                            Mint
                    </Button>
                    

                </div>
            ) : (
                <Text 
                marginTop="70px"
                fontSize="30px"
                letterSpacing="-5.5%"
                fontFamily="VT323"
                textShadow="0 3px #00000"
                color="#999999">Please connect your Wallet.</Text>
            )}
            </Box>
        </Flex>
    );
};

export default MainMint;