import React from "react";
import {
  Box2,
  Container2,
  Row,
  Column,
  FooterLink,
  Heading2,
} from "./FooterStyles";
  
const Footer = () => {
  return (
    <Box2>
      <hr></hr>
      <Container2>
        <Row>
          <Column>
            <Heading2>Copyright</Heading2>
            <FooterLink href="#">&copy; Copyright 2023, Externals. All Rights Reserved.</FooterLink>
          </Column>
          <Column>
            <Heading2>Marketplace</Heading2>
            <FooterLink href="#">Alto</FooterLink>
          </Column>
          <Column>
            <Heading2>Social Media</Heading2>
            <FooterLink href="https://twitter.com/TheExternalss">
              <i className="fab fa-facebook-f">
                <span style={{ marginLeft: "10px" }}>
                  Twitter
                </span>
              </i>
            </FooterLink>
            <FooterLink href="https://discord.gg/dDnEweYsnU">
              <i className="fab fa-instagram">
                <span style={{ marginLeft: "10px" }}>
                  Discord
                </span>
              </i>
            </FooterLink>
          </Column>
        </Row>
      </Container2>
    </Box2>
  );
};
export default Footer;